.sidebar {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 250px;
  position: fixed;
  z-index: 1;
  top: 50px;
  left: 0;
  bottom: 0;
  padding-top: 20px;
  background-color: white;
  box-shadow: grey 0 0 5px;
}

.sidebar-item {
  padding: 0.5em 1em;
  width: calc(100% - 2em);
  text-decoration: none;
  color: black;
}

.sidebar-item.sub {
  padding: 0.5em 1em 0.5em 2em;
  width: calc(100% - 3em);
}

.sidebar-item:hover {
  background-color: #eee;
}

.page-wrapper {
  position: absolute;
  top: 50px;
  left: 250px;
  right: 0;
  padding: 0;
}

.guide-body {
  background-color: #fafafa;
}

.content-wrapper {
  max-width: 900px;
  margin: 0 auto;
}

.page {
  border: 1px solid #555;
  border-radius: 5px;
  padding: 0 20px;
  background-color: white;
}
